export default {
	data() {
		return {
			info: {}
		}
	},
	onLoad(option) {
		global.$http.post('/car/driver/getInfo', {
			params: {
				driver_id: option.id
			},
		}).then(res => {
			if (res.status === "0") {
				this.info = res.data;
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
	}
}
